import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable, Subject } from 'rxjs';
import { IMovimientoInterno } from '../models/movimiento-interno';
import { AuthService } from '@core/auth/auth.service';
import { Response } from '@shared/models/response-with-token';
import { IProveedor } from 'app/modules/administracion/proveedores/models/proveedor';

@Injectable()
export class MovimientosInternosService {
    constructor(private http: HttpClient, private _authService: AuthService) { }
    /**
     * Funcion en el Servicio 
     * @param movimiento_interno 
     * @returns Crea un item nuevo
     */
    saveMovimientoInterno(movimiento_interno:IMovimientoInterno): Observable<IMovimientoInterno> {
        return this.http.post<IMovimientoInterno>(`${environment.stock.movimiento_internos}`, movimiento_interno);
    }

    /**
     * Funcion en el servicio
     * @param movimiento_interno
     * @returns Item actualizado actualizado
     */
    updateMovimientoInterno(movimiento_interno: IMovimientoInterno, idmovimientointerno: number): Observable<IMovimientoInterno> {
        return this.http.put<IMovimientoInterno>(`${environment.stock.movimiento_internos}` + '/' + idmovimientointerno, movimiento_interno);
    }

    /**
     * Funcion en el servicio
     * @param idmovimientointerno 
     * @returns Elimina un item
     */
    deleteMovimientoInterno(idmovimientointerno: number): Observable<IMovimientoInterno> {
        return this.http.delete<IMovimientoInterno>(`${environment.stock.movimiento_internos}` + '/' + idmovimientointerno);
    }

    getDepositos(option?) {
        let f = '?';
        f += !option ? 'filter[activo]=1' : '';
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.depositos}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    getArticulosDepositos(deposito_origen: number) {
        let f = `?filter[deposito]=${deposito_origen}`
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.view_stock_articulo_depositos}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    getUnidadesDeMedidaArticulos(idarticulo) {
        let f = `?filter[idarticulo]=${idarticulo}`
        f += `&expand=um`
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.articulo_unidad_medidas}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    getItems(idmovimientointerno: number) {
        let f = `?filter[idmovimientointerno]=${idmovimientointerno}`;
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.view_stock_movimientos}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }

    getUsuarios(){
        let f = `?sort=asc`;
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.login.view_usuarios}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }
}